import * as React from "react"
import { navigate } from "gatsby"
import * as styles from "../styles/pagination.module.css"

// ---- PCÁî®ÔºàÁúÅÁï•Ë®òÂè∑„ÅÇ„ÇäÔºâ ----
const buildItems = (current, total, edge = 2, around = 1) => {
  // ÂÖàÈ†≠ edge ‰ª∂„ÉªÊú´Â∞æ edge ‰ª∂„ÉªÁèæÂú® around ‰ª∂„ÇíË°®Á§∫„Åó„ÄÅÈñì„ÅØ "‚Ä¶" „ÇíÊåøÂÖ•
  const range = (s, e) => Array.from({ length: e - s + 1 }, (_, i) => s + i)
  const set = new Set()
  range(1, Math.min(edge, total)).forEach(p => set.add(p))
  range(
    Math.max(1, current - around),
    Math.min(total, current + around)
  ).forEach(p => set.add(p))
  range(Math.max(1, total - edge + 1), total).forEach(p => set.add(p))
  const ordered = Array.from(set).sort((a, b) => a - b)

  const items = []
  for (let i = 0; i < ordered.length; i++) {
    if (i > 0 && ordered[i] - ordered[i - 1] > 1) items.push("...")
    items.push(ordered[i])
  }
  return items
}

// ---- „É¢„Éê„Ç§„É´Ôºè‰∏≠ÈñìÂπÖÁî®Ôºà5‰ª∂Âõ∫ÂÆö„ÉªÁúÅÁï•Ë®òÂè∑„Å™„ÅóÔºâ ----
const buildItemsCompact = (current, total, windowSize = 5) => {
  if (total <= windowSize) return Array.from({ length: total }, (_, i) => i + 1)
  const half = Math.floor(windowSize / 2)
  let start = current - half
  let end = current + half
  if (start < 1) {
    start = 1
    end = windowSize
  }
  if (end > total) {
    end = total
    start = total - windowSize + 1
  }
  return Array.from({ length: end - start + 1 }, (_, i) => start + i)
}

/**
 * @param {object} props
 * @param {number} props.current ÁèæÂú®„ÅÆ„Éö„Éº„Ç∏(1Âßã„Åæ„Çä)
 * @param {number} props.totalPages Á∑è„Éö„Éº„Ç∏Êï∞
 * @param {string} [props.basePath="/page"] 2„Éö„Éº„Ç∏ÁõÆ‰ª•Èôç„ÅÆ„Éô„Éº„ÇπÔºàÊú´Â∞æ„Çπ„É©„ÉÉ„Ç∑„É•Êúâ„Çä„ÅßÈÅ∑ÁßªÔºâ
 *   ‰æã) „É´„Éº„Éà: "/page", „Çø„Ç∞: `/tags/${tagKebab}/page`
 */
export default function Pagination({
  current,
  totalPages,
  basePath = "/page",
}) {
  const [input, setInput] = React.useState("")

  // „Éñ„É¨„Éº„ÇØ„Éù„Ç§„É≥„ÉàÔºà‚â§480px „Çí„É¢„Éê„Ç§„É´„ÄÅ‚â§900px „Çí„Éä„É≠„ÉºÊâ±„ÅÑÔºâ
  const [isMobile, setIsMobile] = React.useState(false)
  const [isNarrow, setIsNarrow] = React.useState(false)
  React.useEffect(() => {
    const mqMobile = window.matchMedia("(max-width: 480px)")
    const mqNarrow = window.matchMedia("(max-width: 900px)")
    const update = () => {
      setIsMobile(mqMobile.matches)
      setIsNarrow(mqNarrow.matches)
    }
    update()
    mqMobile.addEventListener?.("change", update)
    mqNarrow.addEventListener?.("change", update)
    return () => {
      mqMobile.removeEventListener?.("change", update)
      mqNarrow.removeEventListener?.("change", update)
    }
  }, [])

  const go = page => {
    if (!page || page < 1 || page > totalPages) return
    // basePath „Åã„Çâ 1„Éö„Éº„Ç∏ÁõÆ„ÅÆ„É´„Éº„Éà„Å® 2„Éö„Éº„Ç∏ÁõÆ‰ª•Èôç„ÅÆ„Éô„Éº„Çπ„ÇíÂ∞éÂá∫
    // ‰æã) basePath="/tags/english/page" ‚Üí root="/tags/english/", pageBase="/tags/english/page/"
    const normalizedBase = String(basePath).replace(/\/+$/, "")
    const rootPathRaw = normalizedBase.replace(/\/page$/, "")
    const rootPath = rootPathRaw === "" ? "/" : rootPathRaw + "/"
    const pageBase = normalizedBase + "/"
    if (page === 1) navigate(rootPath)
    else navigate(pageBase + page + "/")
  }

  // PC: ÁúÅÁï•Ë®òÂè∑„ÅÇ„Çä / ‚â§900px: 5‰ª∂Âõ∫ÂÆöÔºàÁúÅÁï•Ë®òÂè∑„Å™„ÅóÔºâ
  const items = isNarrow
    ? buildItemsCompact(current, totalPages, 5)
    : buildItems(current, totalPages, 2, 1)

  return (
    <nav className={styles.wrap} aria-label="Pagination">
      <button
        className={styles.arrow}
        onClick={() => go(current - 1)}
        disabled={current === 1}
        aria-label="Previous page"
      >
        <span aria-hidden>‚Äπ</span>
      </button>

      <ul className={styles.list}>
        {items.map((it, i) =>
          it === "..." ? (
            <li key={`dots-${i}`} className={styles.dots} aria-hidden>
              ‚Ä¶
            </li>
          ) : (
            <li key={it}>
              <button
                className={`${styles.page} ${
                  it === current ? styles.active : ""
                }`}
                aria-current={it === current ? "page" : undefined}
                onClick={() => go(it)}
              >
                {it}
              </button>
            </li>
          )
        )}
      </ul>

      <button
        className={styles.arrow}
        onClick={() => go(current + 1)}
        disabled={current === totalPages}
        aria-label="Next page"
      >
        <span aria-hidden>‚Ä∫</span>
      </button>

      {/* „É¢„Éê„Ç§„É´Ôºà‚â§480pxÔºâ„ÅØGo‰∏ÄÂºè„ÇíÂá∫„Åï„Å™„ÅÑ„ÄÇPC/‰∏≠ÈñìÂπÖ„ÅØ„É©„Éô„É´ÁÑ°„Åó„ÅßË°®Á§∫ */}
      {!isMobile && (
        <div className={styles.goto}>
          <input
            type="number"
            min={1}
            max={totalPages}
            inputMode="numeric"
            value={input}
            onChange={e => setInput(e.target.value)}
            onKeyDown={e => e.key === "Enter" && go(Number(input))}
            className={styles.input}
            aria-label="Go to page"
          />
          <button className={styles.goBtn} onClick={() => go(Number(input))}>
            Go
          </button>
        </div>
      )}
    </nav>
  )
}
